import React from 'react';
import { connect } from 'react-redux';
import { setVisibilityFilter } from '../actions';

const Link = ({ active, children, onClick }) => (
  <button
    onClick={onClick}
    disabled={active}
    style={{
      marginLeft: '4px',
    }}
  >
    {children}
  </button>
);

// state ã¯ã€€reduxã§ç®¡ç†ã—ã¦ã„ã‚‹å…¨ã¦ã®stateã‚’æŒã£ã¦ã„ã‚‹
// ownPropsã¯connectã—ãŸcomponentæœ¬æ¥ã®propsã‚’å–å¾—ã™ã‚‹
const mapStateToProps = (state, ownProps) => ({
	active: ownProps.filter === state.visibilityFilter
});

const mapDispatchToProps = (dispatch, ownProps) => ({
	onClick: () => dispatch(setVisibilityFilter(ownProps.filter))
});

// ç¬¬ä¸€å¼•æ•°ã¯componentã«æ¸¡ã™propsã‚’åˆ¶å¾¡ã™ã‚‹
// ç¬¬äºŒå¼•æ•°ã¯reducerã‚’å‘¼ã³å‡ºã—ã¦ã€reduxã§ç®¡ç†ã—ã¦ã„ã‚‹stateã‚’æ›´æ–°ã™ã‚‹
// ()å†…ã¯å–å¾—ã—ãŸãƒ‡ãƒ¼ã‚¿ã‚’propsã¨ã—ã¦æ‰±ã„ãŸã„componentã‚’æŒ‡å®šã™ã‚‹
export default connect(
	mapStateToProps,
	mapDispatchToProps
)(Link);
